.charge-list {
  width: 100%;
  border-collapse: collapse;
}

.charge-list th,
.charge-list td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.table-empty {
  text-align: center;
  padding: var(--space-md);
  font-style: italic;
}

.table-empty img.empty-illustration {
  width: 80px;
  margin-bottom: var(--space-sm);
}
